	import Recommend from "@/components/Recommend";
	import {
		getCartList,
		postCartDel,
		changeCartNum,
		getCartCount
	} from "@/api/store";
	import {
		postCollectAll
	} from "@/api/user";
	import {
		mul,
		add
	} from "@/utils/bc";
	import cookie from "@/utils/store/cookie";
	import debounce from "lodash.debounce";

	const CHECKED_IDS = "cart_checked";

	export default {
		name: "ShoppingCart",
		components: {
			Recommend
		},
		props: {},
		data: function() {
			return {
				isIpx: false,
				cartList: {
					invalid: [],
					valid: []
				},
				validList: [],
				isAllSelect: false,
				cartCount: 0,
				countmoney: 0,
				goodsHidden: true,
				footerswitch: false,
				count: 0,
				checkedIds: [],
				loaded: false
			};
		},
		watch: {
			$yroute(n) {
				if (n.name === "ShoppingCart") {
					this.carnum();
					this.countMoney();
					this.getCartList();
					this.gainCount();
					this.goodsHidden = true;
					this.footerswitch = false;
				}
			},
			cartList(list) {
				this.validList = list.valid;
			}
		},
		mounted: function() {
			let that = this;
			that.carnum();
			that.countMoney();
			that.getCartList();
			that.gainCount();
			wx.getSystemInfo({
				success: function(res) {
					console.log(res);
					var name = "iPhone X";
					if (res.model.indexOf(name) > -1) {
						that.isIpx = true;
					}
				}
			});
		},
		methods: {
			goGoodsCon(item) {
				this.$yrouter.push({
					path: "/pages/shop/GoodsCon/index",
					query: {
						id: item.productId
					}
				});
			},
			getCartList: function() {
				let that = this;
				getCartList().then(res => {
					that.cartList = res.data;
					let checkedIds = cookie.get(CHECKED_IDS) || [];
					if (!Array.isArray(checkedIds)) checkedIds = [];
					this.cartList.valid.forEach(cart => {
						if (checkedIds.indexOf(cart.id) !== -1) cart.checked = true;
					});
					if (checkedIds.length) {
						that.checkedIds = checkedIds;
						that.isAllSelect = checkedIds.length === this.cartList.valid.length;
						that.carnum();
						that.countMoney();
					}
					this.loaded = true;
				});
			},
			//删除商品；
			delgoods: function() {
				let that = this,
					id = [],
					valid = [],
					list = that.cartList.valid;
				list.forEach(function(val) {
					if (val.checked === true) {
						id.push(val.id);
					}
				});
				if (id.length === 0) {
           uni.showToast({
              title: "请选择产品",
              icon: "none",
              duration: 2000
            });
					return;
				}
				postCartDel(id).then(function() {
					list.forEach(function(val, i) {
						if (val.checked === false || val.checked === undefined)
							valid.push(list[i]);
					});
					that.$set(that.cartList, "valid", valid);
					that.carnum();
					that.countMoney();
					that.gainCount();
					that.getCartList();
				});
			},
			// //获取数量
			gainCount: function() {
				let that = this;
				getCartCount().then(res => {
					that.count = res.data.count;
				});
			},
			//清除失效产品；
			delInvalidGoods: function() {
				let that = this,
					id = [],
					list = that.cartList.invalid;
				list.forEach(function(val) {
					id.push(val.id);
				});
				postCartDel(id).then(function() {
					list.splice(0, list.length);
					that.gainCount();
					that.getCartList();
				});
			},
			//批量收藏;
			collectAll: function() {
				let that = this,
					data = {
						id: [],
						category: ""
					},
					list = that.cartList.valid;
				list.forEach(function(val) {
					if (val.checked === true) {
						data.id.push(val.product_id);
						data.category = val.type;
					}
				});
				if (data.id.length === 0) {
           uni.showToast({
              title: "请选择产品",
              icon: "none",
              duration: 2000
            });
					return;
				}
				postCollectAll(data).then(function() {
           uni.showToast({
              title: "收藏成功!",
              icon: "none",
              duration: 2000
            });
				});
			},
			//立即下单；
			placeOrder: function() {
				let that = this,
					list = that.cartList.valid,
					id = [];
				list.forEach(function(val) {
					if (val.checked === true) {
						id.push(val.id);
					}
				});
				if (id.length === 0) {
           uni.showToast({
              title: "请选择产品",
              icon: "none",
              duration: 2000
            });
					return;
				}
				console.log(id);
				this.$yrouter.push({
					path: "/pages/order/OrderSubmission/index",
					query: {
						id: id.join(",")
					}
				});
			},
			manage: function() {
				let that = this;
				that.footerswitch = !that.footerswitch;
			},
			goodsOpen: function() {
				let that = this;
				that.goodsHidden = !that.goodsHidden;
			},
			//加
			plus: function(index) {
				let that = this;
				let list = that.cartList.valid[index];
				list.cartNum++;
				if (list.attrInfo) {
					if (list.cartNum >= list.attrInfo.stock) {
						that.$set(list, "cart_num", list.attrInfo.stock);
					}
				} else {
					if (list.cartNum >= list.stock) {
						that.$set(list, "cart_num", list.stock);
					}
				}
				that.carnum();
				that.countMoney();
				that.syncCartNum(list);
			},
			//减
			reduce: function(index) {
				let that = this;
				let list = that.cartList.valid[index];
				if (list.cartNum <= 1) {
           uni.showToast({
              title: "已经是底线啦!",
              icon: "none",
              duration: 2000
            });
					return;
				}
				list.cartNum--;
				if (list.cartNum < 1) {
					that.$set(list, "cart_num", 1);
				}
				that.carnum();
				that.countMoney();
				that.syncCartNum(list);
			},
			syncCartNum(cart) {
				if (!cart.sync) {
					changeCartNum(cart.id, Math.max(cart.cartNum, 1) || 1)
						.then(res => {
							this.getCartList();
						})
						.catch(error => {
							uni.showToast({
								title: error.response.data.msg,
								icon: "none",
								duration: 2000
							});
						});
				}
			},
			//单选
			switchSelect: function(index) {
				let that = this,
					cart = that.cartList.valid[index],
					i = this.checkedIds.indexOf(cart.id);
				cart.checked = !cart.checked;

				if (i !== -1) this.checkedIds.splice(i, 1);
				if (cart.checked) {
					this.checkedIds.push(cart.id);
				}
				let len = that.cartList.valid.length;
				let selectnum = [];
				for (let i = 0; i < len; i++) {
					if (that.cartList.valid[i].checked === true) {
						selectnum.push(true);
					}
				}
				that.isAllSelect = selectnum.length === len;
				that.$set(that, "cartList", that.cartList);
				that.$set(that, "isAllSelect", that.isAllSelect);
				cookie.set(CHECKED_IDS, that.checkedIds);
				that.carnum();
				that.countMoney();
			},
			//全选
			allChecked: function(e) {
				let that = this;
				let selectAllStatus = e.mp.detail.value[0] ? true : false;
				// let selectAllStatus = that.isAllSelect;
				let checkedIds = [];
				// for (let i = 0; i < array.length; i++) {
				//   array[i].checked = selectAllStatus;
				//   checked.push()
				// }
				that.cartList.valid.forEach(cart => {
					cart.checked = selectAllStatus;
					if (selectAllStatus) {
						checkedIds.push(cart.id);
					}
				});
				let cartList = {
					...that.cartList
				};
				that.cartList = [];
				that.cartList = cartList;
				this.$set(this, "cartList", this.cartList);
				this.$set(this, "isAllSelect", selectAllStatus);
				this.checkedIds = checkedIds;
				cookie.set(CHECKED_IDS, checkedIds);
				that.carnum();
				that.countMoney();
				this.$forceUpdate();
			},
			//数量
			carnum: function() {
				let that = this;
				var carnum = 0;
				var array = that.cartList.valid;
				for (let i = 0; i < array.length; i++) {
					if (array[i].checked === true) {
						carnum += parseInt(array[i].cartNum);
					}
				}
				that.$set(that, "cartCount", carnum);
			},
			//总共价钱；
			countMoney: function() {
				let that = this;
				let carmoney = 0;
				let array = that.cartList.valid;
				for (let i = 0; i < array.length; i++) {
					if (array[i].checked === true) {
						carmoney = add(carmoney, mul(array[i].cartNum, array[i].truePrice));
					}
				}
				that.countmoney = carmoney;
			}
		}
	};
